
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    font-family: arial, sans-serif;
    background-color: #F1F2F2;
    min-height: 100vh;
    padding-bottom: 70px;
}

.header {
    background-color: #1A72B9;
    color: #fff;
    padding: 10px 5px;
    text-align: center;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.panel {
    font-size: 20px;
    background-color: #fff;
    color: rgb(59, 59, 59);
    max-width: 500px;
    margin: 30px 25px 30px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 1px 1px 3px -2px rgb(46, 45, 45);
}

.navigation {
    background-color: #fff;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
}

@media(min-width:550px) {
    .panel {
        margin: 30px auto;
    }
}
.header__desc {
    letter-spacing: 1px;
}
.nav {}

.menu {
    display: -webkit-flex;
    display: flex;
    list-style: none;
}

.menu__item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.menu__item--btn {
    padding: 5px;
    min-height: 50px;
    text-align: center;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    color: rgb(87, 82, 82);
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 3px solid #fff;
    transition: .2s linear;
}

.menu__item--icon {
    margin: 5px 0;
    font-size: 20px;
}

.menu__item--btn:hover {
    background-color: #ECF3FC;
    border-bottom: 3px solid #0052ea;
    color: #0052ea;
}

.menu__item--btn:hover .menu__item--icon {
    color: #0052ea;
}

.menu__item--btn.active {
    color: #0052ea;
    border-bottom: 3px solid #0052ea;
    background-color: #CBE2FF;
}
.home {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.home__title {
    letter-spacing: 1px;
    padding-bottom: 10px;
}

.home__item {
    line-height: 150%;
}
.search {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.search__title {
    padding-bottom: 10px;
}

.search__input {
    padding: 5px;
    font-size: 18px;
    border-radius: 5px;
    border: 1px solid gray;
    margin: 0px 0 15px;
    max-width: 300px;
}

.search__list {
    list-style: none;
}

.search__list--el {
    line-height: 150%;
    text-decoration: none;
    color: rgb(59, 59, 59);
}

.search__list--el:hover {
    cursor: pointer;
    text-decoration: underline;
    color: #000;
}

.search__listFields--el {
    line-height: 150%;
    text-transform: capitalize;
}
.university-title {
    font-size: 30px;
    padding-bottom: 10px;
    font-weight: 700;
}

.university {
    list-style: none;

}

/* .university__li {} */

.university__li--el {
    color: rgb(59, 59, 59);
    text-decoration: none;
    line-height: 150%;
}

.university__li--el:hover {
    cursor: pointer;
    text-decoration: underline;
    color: #000;
}
/* .university-page {} */

.university-page__title {
    font-size: 24px;
    font-weight: 700;
    padding-bottom: 10px;
}

.university-page__el {
    padding: 7px 0;
}

.university-page__el--icon {
    font-size: 22px;
}

.university-page__subtitle {
    font-size: 24px;
    padding: 20px 0 10px;
}

.university-page__list {
    list-style: none;
}

.university-page__list--el {
    color: rgb(59, 59, 59);
    text-decoration: none;
    line-height: 150%;
}

.university-page__list--el:hover {
    cursor: pointer;
    text-decoration: underline;
    color: #000;
}
.fieldsOfStudy {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.fieldsOfStudy__el:nth-child(1) {
    font-size: 24px;
    font-weight: 700;
}

.fieldsOfStudy__el {
    padding: 5px 0;

}

.fieldsOfStudy__title {
    margin: 10px 0 5px 0;
    font-weight: 700;
}

.fieldsOfStudy__btn {
    padding: 5px 30px;
    width: 100px;
    margin-left: 10px;
    font-size: 16px;
    background-color: #1A72B9;
    border-style: none;
    border-radius: 5px;
    color: #fff;
    transition: .2s linear;
}

.fieldsOfStudy__btn:hover {
    background-color: #0052ea;
    cursor: pointer;
}

.fieldsOfStudy__studentList {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.fieldsOfStudy__studentList--el {
    line-height: 140%;
}

.fieldsOfStudy__studentList--line {
    height: 3px;
    width: 80%;
    background-color: rgb(128, 128, 128, 0.5);
    border-radius: 35%;
    margin: 5px 0 30px;
}
